import React, { useEffect, useState } from "react";
import { BlockMath } from "react-katex";
import "katex/dist/katex.min.css";
import AnimatedBackground from './AnimatedBackground';


function App() {
    const [loaded, setLoaded] = useState(false);
    const [currentEquation, setCurrentEquation] = useState(0);

    const equations = [
        { eq: `L = \\lambda W` },
        { eq: `y_t = \\phi_1 y_{t-1} + \\phi_2 y_{t-2} + \\dots + \\epsilon_t` },
        { eq: `z = \\min_{x \\in X} \\{ c^T x + \\sum_{k \\in K} \\pi_k (b_k - A_k x) \\}` },
        { eq: `C = S N(d_1) - X e^{-rt} N(d_2)` }
    ];

    useEffect(() => {
      const interval = setInterval(() => {
          setCurrentEquation((prev) => (prev + 1) % equations.length);
      }, 1000); // Change equation every 2 seconds

      setTimeout(() => {
          clearInterval(interval);
          setLoaded(true);
      }, equations.length * 1000); // Stop after showing all equations

      return () => clearInterval(interval);
  }, []);

  if (!loaded) {
      return (
          <div style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              height: "100vh",
              background: "linear-gradient(to bottom, #1C1C1C, #2A2A2A)",
              color: "#FFD700",
              fontFamily: "Avenir, sans-serif",
              fontSize: "1.5rem",
          }}>
              <div style={{ textAlign: "center", position: "relative" }}>
                  <div style={{
                      fontSize: "2rem",
                      color: "#FFD700",
                      fontWeight: "bold",
                      position: "absolute",
                      top: "-3rem",
                      left: "50%",
                      transform: "translateX(-50%)"

                  }}>Loading...</div>
                  <div style={{
                      fontSize: "1.5rem",
                      fontWeight: "bold",
                      textShadow: "0px 4px 10px rgba(0, 0, 0, 0.7)",
                      animation: "fadeInOut 1s ease-in-out infinite",
                  }}>
                      <BlockMath>{equations[currentEquation].eq}</BlockMath>
                      <div style={{ marginTop: "0.5rem", fontSize: "1rem", color: "#FFA500" }}>
                          {equations[currentEquation].description}
                      </div>
                  </div>
              </div>
              <style>
                  {`
                      @keyframes fadeInOut {
                          0% { opacity: 0; }
                          50% { opacity: 1; }
                          100% { opacity: 0; }
                      }
                  `}
              </style>
          </div>
      );
  }

  return (
    <div style={{ fontFamily: "Avenir, sans-serif", lineHeight: 1.6, color: "#CCCCCC", background: "linear-gradient(to bottom, #1C1C1C, #2A2A2A)", margin: 0, padding: 0 }}>
        <header style={{ 
            background: "#1C1C1C", 
            color: "#FFD700", 
            padding: "6rem 0", 
            textAlign: "center",
            position: "relative",    // Added
            overflow: "hidden"       // Added
        }}>
            {/* New component added here */}
            <AnimatedBackground />
            
            <div style={{ 
                display: "flex", 
                justifyContent: "center", 
                alignItems: "center", 
                gap: "1rem",
                position: "relative"  // Added
            }}>
                <h1 style={{ fontSize: "3.5rem", color: "#FFD700", marginBottom: "0.5rem" }}>Yiyang Yu</h1>
                <span style={{ fontSize: "1.5rem", background: "linear-gradient(to right, #FF7F50, #FFD700)", WebkitBackgroundClip: "text", WebkitTextFillColor: "transparent", fontWeight: "600", animation: "glow 3s infinite alternate ease-in-out" }}>OR/ISyE @ Georgia Tech</span>
            </div>
            <div style={{ 
                marginTop: "1.5rem", 
                display: "flex", 
                justifyContent: "center", 
                gap: "2rem",
                position: "relative"  // Added
            }}>
                <a href="https://www.linkedin.com/in/y3y/" target="_blank" rel="noopener noreferrer" style={{ color: "#CCCCCC", fontSize: "1.2rem", textDecoration: "none" }}>LinkedIn</a>
                <a href="mailto:yyu473@gatech.edu" style={{ color: "#CCCCCC", fontSize: "1.2rem", textDecoration: "none" }}>Contact</a>
                <a href="#" style={{ color: "#CCCCCC", fontSize: "1.2rem", textDecoration: "none" }}>Resume</a>
            </div>
        </header>
            <div style={{ maxWidth: "1100px", margin: "auto", overflow: "hidden", padding: "0 2rem" }}>
                {"Optimization  Data Science  Supply Chain  Finance".split("  ").map((section, index) => (
                    <section
                        key={section}
                        id={section.toLowerCase().replace(/ /g, "-")}
                        style={{
                            margin: "4rem 0",
                            display: "flex",
                            alignItems: "center",
                            background: "linear-gradient(to right, #292929, #3A3A3A)",
                            borderRadius: "15px",
                            padding: "2rem",
                            boxShadow: "0 5px 15px rgba(0, 0, 0, 0.5)",
                            transform: loaded ? "translateY(0)" : "translateY(50px)",
                            opacity: loaded ? 1 : 0,
                            transition: `all 0.5s ease ${index * 0.2}s`,
                            color: "#FFD700"
                        }}
                    >
                        <div style={{ flex: 1, textAlign: "center", fontSize: "1.5rem", fontWeight: "bold", color: "#FFD700" }}>{section}</div>
                        <div
                            style={{
                                flex: 2,
                                padding: "1.5rem",
                                background: "#1F1F1F",
                                borderRadius: "10px",
                                boxShadow: "0 3px 10px rgba(0, 0, 0, 0.5)",
                                color: "#EAEAEA",
                                textAlign: "left",
                                fontSize: "1.2rem",
                                fontStyle: "italic",
                                lineHeight: "1.8",
                                transition: "transform 0.3s ease-in-out",
                                transform: "scale(1)",
                            }}
                            onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
                            onMouseLeave={(e) => e.currentTarget.style.transform = "scale(1)"}
                        >
                            {section === "Optimization"
                                ? "Explore techniques to optimize systems, processes, and decision-making frameworks to achieve maximum efficiency."
                                : section === "Data Science"
                                ? "Dive into data analysis, predictive modeling, and machine learning to unlock insights and drive innovation."
                                : section === "Supply Chain"
                                ? "Learn about end-to-end supply chain management, logistics optimization, and sustainable practices."
                                : "Understand financial systems, investment strategies, and methods to drive growth and stability."}
                        </div>
                    </section>
                ))}
            </div>
            <footer style={{ background: "#1A1A1A", color: "#CCCCCC", textAlign: "center", padding: "1rem 0", marginTop: "2rem" }}>
                <p>&copy; 2025 Professional Website. All rights reserved.</p>
            </footer>
            <style>
                {`
                    @keyframes glow {
                        0% { text-shadow: 0 0 5px #FFD700, 0 0 10px #FF7F50; }
                        100% { text-shadow: 0 0 15px #FFD700, 0 0 20px #FF7F50; }
                    }

                    @keyframes fadeInOut {
                        0% { opacity: 0; }
                        50% { opacity: 1; }
                        100% { opacity: 0; }
                    }
                `}
            </style>
        </div>
    );
}

export default App;
