import React, { useEffect, useRef } from 'react';

const AnimatedBackground = () => {
  const canvasRef = useRef(null);
  
  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    let animationFrameId;
    
    const setCanvasSize = () => {
      canvas.width = canvas.offsetWidth;
      canvas.height = canvas.offsetHeight;
    };
    
    setCanvasSize();
    window.addEventListener('resize', setCanvasSize);
    
    // Generate random number of time series (0-7)
    const generateTimeSeriesCount = () => Math.floor(Math.random() * 8);
    
    // Modified to include opacity for fading
    let allSeries = [];
    
    const generateTimeSeries = () => {
      const timeSeriesCount = generateTimeSeriesCount();
      allSeries = [];
      for (let series = 0; series < timeSeriesCount; series++) {
        const points = [];
        const segments = 100;
        let prevY = canvas.height / 2;
        
        for (let i = 0; i <= segments; i++) {
          const change = (Math.random() - 0.5) * (canvas.height / 16);
          const y = Math.max(Math.min(prevY + change, canvas.height * 0.7), canvas.height * 0.3);
          
          points.push({
            x: (canvas.width * i) / segments,
            y: y
          });
          
          prevY = y;
        }
        allSeries.push({
          points,
          opacity: 1.0 // Add opacity property for fading
        });
      }
    };
    
    const drawGrid = () => {
      ctx.beginPath();
      ctx.strokeStyle = 'rgba(255, 215, 0, 0.1)';
      ctx.lineWidth = 1;
      
      for (let x = 0; x < canvas.width; x += canvas.width / 20) {
        ctx.moveTo(x, 0);
        ctx.lineTo(x, canvas.height);
      }
      
      for (let y = 0; y < canvas.height; y += canvas.height / 10) {
        ctx.moveTo(0, y);
        ctx.lineTo(canvas.width, y);
      }
      
      ctx.stroke();
    };
    
    let progress = 0;
    const draw = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      drawGrid();
      
      const drawPosition = progress * canvas.width;
      
      // Draw and update each series
      allSeries = allSeries.filter(series => series.opacity > 0);
      
      allSeries.forEach((series, seriesIndex) => {
        const points = series.points;
        
        // Draw the main line
        ctx.beginPath();
        let started = false;
        
        for (let i = 0; i < points.length; i++) {
          if (points[i].x <= drawPosition) {
            if (!started) {
              ctx.moveTo(points[i].x, points[i].y);
              started = true;
            } else {
              ctx.lineTo(points[i].x, points[i].y);
            }
          }
        }
        
        // Style for the main line with fading opacity
        ctx.strokeStyle = `rgba(255, 215, 0, ${(0.3 - seriesIndex * 0.1) * series.opacity})`;
        ctx.lineWidth = 2;
        ctx.stroke();
        
        // Add glow effect at drawing point
        if (drawPosition > 0 && drawPosition < canvas.width) {
          // Find the points before and after the current drawing position
          let beforePoint = points[0];
          let afterPoint = points[0];
          for (let i = 0; i < points.length - 1; i++) {
            if (points[i].x <= drawPosition && points[i + 1].x > drawPosition) {
              beforePoint = points[i];
              afterPoint = points[i + 1];
              break;
            }
          }
          
          // Draw glowing segment with fading opacity
          ctx.beginPath();
          ctx.moveTo(beforePoint.x, beforePoint.y);
          ctx.lineTo(afterPoint.x, afterPoint.y);
          ctx.strokeStyle = `rgba(255, 215, 0, ${0.8 * series.opacity})`;
          ctx.lineWidth = 3;
          ctx.shadowBlur = 20;
          ctx.shadowColor = `rgba(255, 215, 0, ${series.opacity})`;
          ctx.stroke();
          ctx.shadowBlur = 0;
        }
        
        // Start fading when drawing is complete
        if (progress >= 1) {
          series.opacity -= 0.01;
        }
      });
      
      progress += 0.002;
      
      // Generate new series when all current ones have faded
      if (progress >= 1 && allSeries.length === 0) {
        progress = 0;
        generateTimeSeries();
      }
      
      animationFrameId = requestAnimationFrame(draw);
    };
    
    generateTimeSeries();
    draw();
    
    return () => {
      window.removeEventListener('resize', setCanvasSize);
      cancelAnimationFrame(animationFrameId);
    };
  }, []);
  
  return (
    <canvas
      ref={canvasRef}
      style={{
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        opacity: 0.2,
        pointerEvents: 'none',
      }}
    />
  );
};

export default AnimatedBackground;